.light-theme{
  --bgVar:rgba(233, 192, 244, 0.652)  ;

  --bgcolvar: rgba(0, 0, 0, 0.797);
  --bgbackvar:rgba(255, 255, 255, 0.771);
  
}
.dark-theme{
  --bgVar: #282c34;
  --bgcolvar: rgba(255, 255, 255, 0.821);
  --bgbackvar:rgba(0, 0, 0, 0.811);
}

body{
  z-index: 20 !important;
  background-color: var(--bgVar) ;
}
.backAndTheme{
  display: flex;
  width: 50rem;
  max-width: 95%;
  margin: 4rem auto;
  padding: 0px 15px;
}
.back-btn-icon{
  color: rgb(19, 0, 0);
  font-size: 25px;
  font-weight: bold;
  
}